.modal {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.4);
  position: fixed;
  /* top: 0;
  left: 0; */
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  transition: 0.5s;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
}

.modal__content {
  padding: 15px;
  border-radius: 12px;
  background-color: #fff;

  width: 50vh;
  transform: scale(0.5);
  transition: 0.4s all;
}

.modal__content.active {
  transform: scale(1);
}

.modal__close {
  width: 20px;
  height: 20px;
  position: relative;
  top: 0%;
  left: 95%;
  transition: 0.5s all;
}

.modal__close:hover,
.modal__close:hover {
  color: blue;
}
